import { Button } from '@mui/material'
import { useState, useEffect } from 'react'
import './App.css'
import styled from '@emotion/styled'

import Counter from './Counter'

const CounterList = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
`

const initPlayers = (names) => names.map((name, i) => ({ name, millis: 0, order: i }))

const App = () => {
  const [players, setPlayers] = useState(initPlayers(['Lauri', 'Timi', 'Mikko']))
  const [activePlayer, setActivePlayer] = useState('')

  const getNextActivePlayer = (name) => {
    const idx = players.findIndex(player => player.name === name)
    const inBounds = idx >= players.length - 1
    return inBounds ? players[0].name : players[idx + 1].name
  }

  const handlePass = (playerObj) => {
    setPlayers(players.map(player => player.name === playerObj.name ?
      playerObj : player
    ))
    setActivePlayer(getNextActivePlayer(activePlayer))
  }

  const savePlayerTime = (playerObj) => {
    setPlayers(players.map(player => player.name === playerObj.name ?
      playerObj : player
    ))
  }

  const passTurn = () => {
    setActivePlayer(getNextActivePlayer(activePlayer))
  }

  useEffect(() => {
    setActivePlayer(players[0].name)
  }, [])

  return (
    <div className="App">
      <Button onClick={() => setActivePlayer('')}>
          Pause
      </Button>
      <CounterList>
      {
        players.map(player =>
          <Counter
            key={player.name} playerInfo={player}
            paused={activePlayer !== player.name}
            onPass={passTurn}
            savePlayerTime={savePlayerTime}
            stealActivePlayer={() => setActivePlayer(player.name)}
            globalPause={activePlayer === ''}
          />
        )
      }
      </CounterList>
    </div>
  )
}

export default App
