import { useState, useEffect } from 'react'
import dayjs from 'dayjs'
import Button from '@mui/material/Button'
import styled from '@emotion/styled'

const CounterContainer = styled.div`
  background-color: ${p => p.paused ? '#3c414d' : '#202526'};
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border: 5px;
  border-radius: 10px;
  border-color: white;
  max-width: 600px;
  margin: 10px;
  height: 40vh;
  min-width: 60vh;
`

const CounterTitle = styled.text`
  font-size: 2em;
  font-weight: bold;
  margin-block-end: 0px;
`

const Counter = ({ playerInfo, onPass, savePlayerTime, paused, stealActivePlayer, globalPause }) => {
  const [started, setStarted] = useState(dayjs())
  const [current, setCurrent] = useState(dayjs())
  let handle = 0
  useEffect(() => {
    setCurrent(dayjs())
    if (!paused) {
      setStarted(dayjs())
      handle = setInterval(() => {
        setCurrent(dayjs())
      }, 250)
    } else {
      clearInterval(handle)
      savePlayerTime({
        ...playerInfo,
        millis: playerInfo.millis + dayjs().diff(started, 'ms')  
      })
    }
    
    setCurrent(dayjs())
    setStarted(dayjs())
    return () => clearInterval(handle)
  }, [paused])

  const handlePass = () => {
    onPass()
    setCurrent(dayjs())
  }

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins !== 0 ? `${mins}:${secs < 10 ? '0' : ''}` : ''}${secs}`
  }

  return (
    <CounterContainer paused={paused} onClick={() => !paused ? handlePass() : {}}>
      <CounterTitle>
        {playerInfo.name}
        <br />
        {formatTime(
          Math.floor(
            (playerInfo.millis / 1000) + current.diff(started, 'seconds')
          )
        )}
      </CounterTitle>
      {!paused &&
        <Button
          variant="contained"
          autoFocus
          onClick={handlePass}>
          {!paused ? 'Pass' : 'Start'}
        </Button>}
        {globalPause && <Button
          variant="outlined"
          onClick={(e) => {
            e.stopPropagation()
            stealActivePlayer()}
          }>
          Start
        </Button>}
    </CounterContainer>
  )
}

export default Counter